@import "sass/index";
@import "components/AutoOnOptions/style.module.scss";

.title {
  color: use(--template-page-joinBox-text);
}

.checkbox {
  color: use(--template-page-joinBox-text);
  margin: 0;
}

.check {
  color: use(--template-page-joinBox-text);
}

label.label,
.label {
  color: use(--template-page-joinBox-text);
}

.rootInput {
  &:focus {
    border: 2px solid $mui-secondary-main;
  }
}

.radio {
  border-color: use(--template-page-joinBox-text);
  &Round {
    background-color: use(--template-page-joinBox-text);
  }
}
