  max-width: 900px;
  margin: 0 auto;
}

.operation {
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  grid-gap: 10px;
  gap: 10px;
}

.limits {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
  grid-gap: 10px;
  gap: 10px;
}

